import React, {useState/*, useEffect*/} from 'react';

//import logo from './CELECTOR_LOGO_NEW_32X32.png';
import icon from './iconNavMob.png';
import './NavMob.css';


function NavMob({setLink, items}) {
    const [open, setOpen] = useState(false);

    const HandleClick = value => () => {
        setLink("./" + value);
    }


    function DropdownMenu(props) { 

        if(!open) return null;
        else {
            return (
                    <ul className="NavMob-Dropdown">
                        {props.children}
                    </ul>
            )
        }
    }



    function NavMobItem(props) {
  
        return (
          <li className="NavMob-Item" >
            <a className="NavMob-Item-Link" onClick={HandleClick(props.text)}> {props.text} </a>
          </li>  
        );
    }




    return (
      <div className="NavMob">

        <img src={icon} className="NavMob-Icon" alt="---" onClick={() => setOpen(!open)} />

          <DropdownMenu >
              <NavMobItem id="1" text={items[1]} />
              <NavMobItem id="2" text={items[2]} />
              <NavMobItem id="3" text={items[3]} />
              <NavMobItem id="4" text={items[4]} />
              <NavMobItem id="5" text={items[5]} />
              <NavMobItem id="6" text={items[6]} />
              <NavMobItem id="7" text={items[7]} />
              <NavMobItem id="8" text={items[8]} />
          </DropdownMenu>
      </div>
    )

  }

export default NavMob;